<template>
    <div>
        <h3 class="notfound">404</h3>
        <p class="margin-t-20 text-center">
            <Button @click="goHome" type="primary" size="large">返回首页</Button>
        </p>

    </div>
</template>
<script>
    /**
     * @Description: 404
     * @Props:
     */

    export default {
        name: '404',
        data () {
            return {}
        },
        mounted () {
        },
        methods: {
            goHome () {
                this.$router.push('/home');
            }
        },
    }
</script>
<style lang="less" scoped>
    .notfound {
        margin-top: 80px;
        font-size: 60px;
        text-align: center;
    }
</style>


